h1 {
    margin: 0px auto 50px auto;
    text-align: center;
    font-weight: 400;
    font-size: 30px;
}

.field-text{
    font-size: 2em;
    color: #505050;
    font-weight: 400;
    margin-top: -10px;
    padding: 8px;
    display: block;
}
.label-height::after{
    content: ' in';
    font-size: 0.5em;
    color: #4E5279;
    font-weight: 400;
    text-align: center;
    margin-top: -12px;
}  
.label-weight:after{
    content: ' lbs';
    font-size: 0.5em;
    color: #4E5279;
    font-weight: 400;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
