.bmiContainer {
    height: 100vh;
}

.formGroup {
    width: 650px;
    border-radius: 8px;
    background-color: #fff;
    padding: 20px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0px;
    right: 0px;
    margin: 0px auto;
    border: 1px solid rgba(128, 128, 128, 0.13);
    box-shadow: 5px 7px 10px rgba(128, 128, 128, 0.34);
}

.weight, .height {
    margin-top: 8px;
}

input[type=range] {
    -webkit-appearance: none;
    height: 3px;
    background: #27283A;
    border-radius: 5px;
    margin-top: 25px;
    width: 100%;
  }
  
  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }
  
  input[type=range]:focus {
    outline: none; 
  }
  
  input[type=range]::-ms-track {
    width: 100%;
    cursor: pointer;
    background: transparent; 
    border-color: transparent;
    color: transparent;
  }
  
  input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    background: #007bff;
    cursor: pointer; 
  }
  
  input[type=range]::-moz-range-thumb {
    height: 14px;
    width: 14px;
    border-radius: 50%;
    background: #007bff;
    cursor: pointer;
  }
  
  input[type=range]::-ms-thumb {
    height: 14px;
    width: 14px;
    border-radius: 50%;
    background: #007bff;
    cursor: pointer;
  }

.defaultBtn {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
    display: block;
    width: 100%;
    margin: 20px auto;
    font-weight: 400;
    text-align: center;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
